
.calculator-container {
  background: linear-gradient(145deg, #ffffff 10%, #e0f2fe 90%);
  color: #1e293b;
  border-radius: 16px;
  padding: 40px 30px;
  box-shadow: 0 4px 25px rgba(59, 130, 246, 0.15);
  max-width: 700px;
  margin: 50px auto;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.calculator-container:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 35px rgba(59, 130, 246, 0.25);
}


.calculator-container h2 {
  text-align: center;
  color: #2563eb;
  font-size: 2rem;
  margin-bottom: 25px;
  font-weight: 700;
  letter-spacing: 1px;
  position: relative;
}

.calculator-container h2::after {
  content: "";
  width: 90px;
  height: 3px;
  background: linear-gradient(90deg, #3b82f6, #a78bfa);
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 5px;
}

.calculator-container form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-group {
  display: flex;
  flex-direction: column;
}

.input-group label {
  font-size: 0.95rem;
  font-weight: 600;
  margin-bottom: 6px;
  color: #334155;
}

.calculator-container input,
.calculator-container select,
.calculator-container textarea {
  width: 100%;
  padding: 12px 15px;
  border-radius: 10px;
  border: 1px solid #cbd5e1;
  background: rgba(248, 250, 252, 0.9);
  color: #1e293b;
  font-size: 1rem;
  outline: none;
  transition: border 0.3s ease, box-shadow 0.3s ease;
}

.calculator-container input:focus,
.calculator-container select:focus,
.calculator-container textarea:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 10px rgba(59, 130, 246, 0.3);
}

.calculator-container button {
  background: linear-gradient(90deg, #3b82f6, #a78bfa);
  border: none;
  color: #fff;
  padding: 14px;
  border-radius: 10px;
  font-size: 1.05rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 10px rgba(59, 130, 246, 0.25);
}

.calculator-container button:hover {
  background: linear-gradient(90deg, #2563eb, #7c3aed);
  box-shadow: 0 0 20px rgba(124, 58, 237, 0.3);
  transform: scale(1.03);
}

.result-box {
  margin-top: 30px;
  background: rgba(255, 255, 255, 0.85);
  border-radius: 12px;
  padding: 25px;
  border: 1px solid rgba(59, 130, 246, 0.2);
  box-shadow: 0 4px 20px rgba(59, 130, 246, 0.1);
  animation: fadeIn 0.5s ease-in-out;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.result-box p {
  margin-bottom: 10px;
}

.result-title {
  color: #1d4ed8;
  font-size: 1.3rem;
  margin-bottom: 15px;
  border-bottom: 1px solid rgba(59, 130, 246, 0.3);
  padding-bottom: 5px;
  font-weight: 600;
}

.result-content {
  color: #334155;
  font-size: 1rem;
  line-height: 1.7;
}

.error-message {
  background: rgba(254, 202, 202, 0.3);
  border: 1px solid rgba(239, 68, 68, 0.5);
  color: #dc2626;
  padding: 12px;
  border-radius: 8px;
  margin-top: 15px;
  text-align: center;
  font-weight: 600;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}


@media (max-width: 600px) {
  .calculator-container {
    padding: 25px 20px;
  }

  .calculator-container h2 {
    font-size: 1.6rem;
  }

  .calculator-container input,
  .calculator-container select,
  .calculator-container textarea {
    font-size: 0.95rem;
  }

  .calculator-container button {
    font-size: 0.95rem;
    padding: 12px;
  }
}
