.transactions-container {
  padding: 20px;
  background: #fff;
  border-radius: 10px;
}

.transactions-form {
  display: grid;
  gap: 10px;
  margin-bottom: 20px;
}

.transactions-form input,
.transactions-form select,
.transactions-form textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.transactions-form button {
  background: #4e73df;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.transactions-table {
  width: 100%;
  border-collapse: collapse;
}

.transactions-table th,
.transactions-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

.transactions-table button {
  padding: 5px 8px;
  margin: 2px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.transactions-table button:first-child {
  background-color: #2196f3;
  color: white;
}

.transactions-table button:last-child {
  background-color: #f44336;
  color: white;
}
