.consultation-management-main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  background: #f3f6fa;
}

.consultation-card {
  background: #fff;
  border-radius: 15px;
  box-shadow: 0 4px 24px rgba(44,62,80,0.12);
  padding: 36px 40px;
  min-width: 900px;
  max-width: 100%;
  margin: 30px 0;
}

.consultation-header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.add-btn {
  padding: 8px 18px;
  background: #52a869;
  color: #fff;
  font-weight: 600;
  border-radius: 7px;
  border: none;
  cursor: pointer;
  font-size: 15px;
  transition: background 0.18s;
}

.add-btn:hover {
  background: #418b56;
}

.consultation-form {
  background: #f6fbfd;
  padding: 18px;
  border-radius: 8px;
  margin-bottom: 22px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 13px;
}

.consultation-form input,
.consultation-form select,
.consultation-form textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 15px;
  background: #fbfbf8;
}

.consultation-form textarea {
  min-height: 40px;
  resize: vertical;
  grid-column: span 3;
}

.form-actions {
  grid-column: span 3;
  display: flex;
  gap: 12px;
}

.consultation-form button {
  background: #52a869;
  color: white;
  padding: 10px 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
}

.consultation-form button:hover {
  background: #418b56;
}

.cancel-btn {
  background: #ccc !important;
  color: #333 !important;
}

.cancel-btn:hover {
  background: #bbb !important;
}

.search-row {
  width: 100%;
  margin-bottom: 16px;
}
.search-bar {
  width: 350px;
  max-width: 100%;
  padding: 11px;
  border-radius: 7px;
  border: 1px solid #ced4da;
  font-size: 15px;
}

.table-container {
  overflow-x: auto;
}

.consultations-table {
  width: 100%;
  border-collapse: collapse;
  background: #fff;
}

.consultations-table th,
.consultations-table td {
  border: 1px solid #e1e4e8;
  padding: 12px 10px;
  text-align: center;
  font-size: 15px;
  vertical-align: middle;
}

.consultations-table th {
  background: #f2f3f6;
  font-weight: 700;
}

.consultations-table tr:nth-child(even) {
  background: #fafbfc;
}

.consultations-table button {
  padding: 6px 12px;
  margin: 2px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.15s;
}

.consultations-table button:first-child {
  background-color: #2196f3;
  color: white;
}

.consultations-table button:first-child:hover {
  background-color: #1769aa;
}

.consultations-table button:last-child {
  background-color: #f44336;
  color: white;
}

.consultations-table button:last-child:hover {
  background-color: #d32f2f;
}

@media (max-width: 1100px) {
  .consultation-card { min-width: 100%; padding: 24px 6px; }
  .consultation-form { grid-template-columns: 1fr; }
  .search-bar { width: 100%; }
}
